<template>
  <div>
    <h1>回報工具</h1>
    <div>
      <button @click='report'>report</button>
      <p>id range</p>
      <input type="number" v-model="cid" style="width: 60px;">
      班
      <input type="number" v-model="idStart" style="width: 60px;">~
      <input type="number" v-model="idEnd" style="width: 60px;">
    </div>
    <div>
      <input type="checkbox" name="is-covid" id="is-covid" v-model="isCovid">
      <label for="is-covid"> is covid </label>
    </div>
    <div>
      <p>paste report here</p>
      <textarea v-model="inputText"></textarea>
      <br>
      <button @click='enter'>Enter</button>
    </div>
    <div>
      <p>
        {{ summary }}
      </p>
      <p v-for="entry, i in entries" :key="i">
        <button @click='del(i)' style="user-select: none;">x</button>
        <template v-for="line, j in entry.report.split('\n')" :key="j">
          <br>
          <span>{{ line }}</span>
        </template>
      </p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      inputText: "",
      cid: 8,
      idStart: 118,
      idEnd: 133,
      isCovid: false,
      entries: []
    }
  },
  methods: {
    enter() {
      this.inputText = this.inputText.trim()
      if (this.inputText.length > 0) {

        let matchID = this.inputText.match(/學號\s*(：|:)\s*(\d+)/)
        if (matchID) {
          console.log(matchID)
          let id = +matchID[2]
          this.entries = this.entries.filter(x => x.id !== id)
          this.entries.push({
            id,
            report: this.inputText
          })

          this.inputText = ""
          this.entries = this.entries.sort((a, b) => {
            return a.id - b.id
          })
        }
      }
    }, del(id) {
      if (confirm("delete?"))
        this.entries.splice(id, 1)
    }, report() {
      let copyText = this.summary
      copyText += "\n\n"
      for (let item of this.entries) copyText += item.report + "\n\n"
      prompt("copy this", copyText)
    }
  }, computed: {
    summary() {
      let total = this.idEnd - this.idStart + 1
      let s = `${this.cid}班${total}員`
      let missing = []
      for (let id = this.idStart; id <= this.idEnd; id++) {
        if (this.entries.some(x => x.id == id)) continue
        missing.push(id.toString().padStart(3, "0"))
      }

      if (this.isCovid) {

        let covid = []

        for (let item of this.entries) {
          if (item.report.match(/陽性/)) covid.push(item.id.toString().padStart(3, "0"))
        }
        s += `\n陰性${this.entries.length - covid.length}員 陽性${covid.length}員 ${covid.join("、")}`
      }

      if (missing.length > 0) {
        s += `\n未回報${missing.length}員 ${missing.join("、")}`
      }
      return s
    }
  }
}
</script>

